// $font-family-Roboto: 'Roboto', sans-serif;
$font-family-Jost: 'Jost', sans-serif;

$font-family-base: $font-family-Jost;

$font-size-small: 12px;
$font-size-medium: 16px;
$font-size-large: 20px;
$font-size-x-large: 32px;

$color-white: #fff;

$color-primary: #c10303;
$color-primary-dark: #910000;

$color-secondary: #e89a00;
$color-secondary-light: #faf1e1;

// $color-overlay: hsla(40, 100%, 45%, .7);
$color-overlay: hsla(0, 0%, 100%, .8);

$transition-base-duration: 200ms;
$transition-base-easing: ease-out;

