.dropdown {
    position: relative;
    background-color: $color-white;
    min-width: 140px;
    width: fit-content;
    color: $color-primary;
    font-size: $font-size-medium;
    font-weight: 500;

    &__list {
        position: absolute;
        width: 100%;
        // border-top: 2px solid $color-primary;

        &--hide {
            display: none;
        }
    }

    &__main,
    &__option {
        display: flex;
        padding: 6px 10px;
        background-color: $color-white;
        cursor: pointer;
    }

    &__main {
        display: flex;
        align-items: center;
        justify-content: space-between;
        // border: none;
        // box-shadow: none;

        & .triangle {
            transform: rotate(180deg);
            width: 9px;
            height: 8px;
            transition: transform $transition-base-duration
                $transition-base-easing;
            // transition: transform 1s;

            &__path {
                fill: $color-primary;
            }
        }

        &--open {
            // border: 2px solid $color-primary;
            // box-shadow: inset 0px 0px 0px 2px $color-primary;
            background-color: $color-primary;
            color: $color-white;

            & .triangle {
                transform: rotate(0deg);

                &__path {
                    fill: $color-white;
                }
            }
        }
    }
}
