.header {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 90px;

    &--large {
        align-items: flex-end;
        min-height: 205px;
    }

    &--withHome {
        justify-content: space-between;
    }

    &__homeButton {
        display: flex;
        width: 40px;
        height: 40px;
        color: $color-primary;

        & > * {
            width: 100%;
            height: 100%;
        }
    }
}