.modal {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    top: 0;
    height: 0;
    width: 100vw;
    height: 100vh;
    background-color: $color-overlay;

    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    &__inner {
        position: relative;
    }

    &__close {
        display: flex;
        position: absolute;
        top: 2px;
        right: 2px;
        cursor: pointer;
        
        & svg {
            width: 34px;
            height: 34px;
        }
        
        & path {
            stroke-width: 4px;
            stroke: $color-secondary;
        }
    }
}