.orderControls {
    display: flex;
    margin-left: auto;

    &__add,
    &__remove {
        display: flex;
        // width: fit-content;
        // height: fit-content;
        width: 40px;
        height: 40px;
        background-color: $color-secondary;
        cursor: pointer;

        &:first-of-type {
            margin-right: 10px;
        }

        & > svg {
            width: 40px;
            height: 40px;

            & > path {
                stroke: $color-primary-dark;
                stroke-width: 3;
            }
        }
    }
}