.cartPage {
    &__main {
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        & .order {
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            flex: 1;
            margin-bottom: 50px;
            &__content {
                flex: 1;
                height: fit-content;
            }
        }
    }
}
