html,
body {
    margin: 0;
    padding: 0;
    height: 100vh;
    box-sizing: border-box;
}

body {
    background-color: $color-secondary-light;
}

#root,
.app,
.page,
main {
    height: 100%;
}

main {
    display: flex;
    flex-direction: column;
}

h2 {
    font-size: $font-size-x-large;
    font-weight: 500;
    color: $color-primary-dark;
    text-align: center;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.page {
    display: flex;
    flex-direction: column;
}

.container {
    width: calc(100vw - 80px);
    margin: 0 auto;
    max-width: 1000px;
}
