.input {
    display: flex;
    flex-direction: column;
    color: $color-primary;
    margin-bottom: 20px;

    & > label {
        font-size: $font-size-small;
        font-weight: 500;
        margin-bottom: 4px;
    }

    & > input {
        background-color: $color-white;
        font-size: $font-size-medium;
        padding: 8px;
        border: none;
        color: $color-primary;
        font-weight: bold;

        &::placeholder {
            color: $color-primary;
            font-weight: normal;
            opacity: 0.6;
        }
    }
}
