.button {
    width: 200px;
    // height: 60px;
    height: fit-content;
    font-size: $font-size-x-large;
    font-weight: 500;
    color: $color-white;
    background-color: $color-primary;
    border: none;
    padding: 7px 0;
    cursor: pointer;

    &--secondary {
        background-color: $color-secondary;
    }

    &--order {
        position: absolute;
        bottom: 0;
        margin: 0 auto;
        z-index: 20;
    }

    &--textOnly {
        background-color: transparent;
        color: $color-secondary;
        font-size: $font-size-large;
        // height: 68px;
    }
}

.buttonGroup {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    gap: 20px;
}
