.productModal {
    display: flex;
    flex-direction: column;
    color: $color-white;

    & .modal {
        &__inner {
            width: 295px;
            height: 403px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $color-primary;
        height: fit-content;
        padding: 20px;
    }

    &__imageContainer {
        width: 100px;
        height: 100px;
        padding: 10px;
        margin-bottom: 20px;
        background-color: $color-secondary;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    &__productName {
        display: flex;
        font-size: $font-size-medium;
        text-align: center;
        margin: 0;
        font-weight: 500;
    }

    &__productDescription {
        display: flex;
        font-size: $font-size-small;
        text-align: center;
        margin: 0;
        line-height: 14px;
    }

    &__orderList {
        display: flex;
        flex-direction: column;
        background-color: $color-primary-dark;
        margin-bottom: 20px;
        padding: 20px;
    }

    &__order {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        height: fit-content;
        font-size: $font-size-large;

        & > * {
            width: fit-content;
        }

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    &__orderCount {
        display: flex;
        width: 34px;
        color: $color-secondary;
        font-weight: bold;
    }

    &__orderName {
        font-weight: 500;
    }

    &__orderCost {
        margin-left: 6px;
        color: $color-secondary;
        font-weight: 500;
    }
}
