.order {
    background-color: $color-primary-dark;
    color: $color-white;

    & > * {
        padding: 20px;
    }

    &__content {
        &--extendable {
            cursor: pointer;
        }
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: $font-size-large;
        font-weight: bold;
    }

    &__arrowContainer {
        transition: transform $transition-base-duration $transition-base-easing;
        &--extended {
            transform: rotate(-180deg);
        }
    }

    &__arrow {
        fill: $color-white;
        transform: rotate(180deg);
    }

    &__collapseArea {
        margin-top: 20px;
        &--closed {
            display: none;
        }
    }

    &__footer {
        background-color: $color-primary;
        &--closed {
            display: none;
        }
    }

    &__total {
        display: flex;
        justify-content: space-between;
        font-weight: bold;
    }

    &__deliveryTime {
        display: flex;
        justify-content: center;
    }

    &__deliveryTimeLabel {
        margin-right: 10px;
    }
}

.orderElement {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    height: fit-content;
    font-size: $font-size-large;

    & > * {
        width: fit-content;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    &__orderAmount {
        display: flex;
        width: 34px;
        color: $color-secondary;
        font-weight: bold;
    }

    &__uniquePizza {
        display: flex;
        flex-direction: column;
        font-weight: 500;
    }

    &__pizzaSize {
        display: flex;
        color: $color-secondary-light;
        font-size: $font-size-medium;
        // font-weight: bold;
        font-weight: normal;
    }

    &__pizzaCost {
        margin-left: 10px;
    }
}

.orderList {
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: auto;
    width: 100%;
}
