.productCard {
    width: 120px;
    display: flex;
    flex-direction: column;
    cursor: pointer;

    &__imageContainer {
        box-sizing: border-box;
        height: 120px;
        padding: 10px;
        background-color: $color-primary-dark;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    &__name {
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: $color-white;
        font-weight: 500;
        font-size: $font-size-medium;
        background-color: $color-primary;
        padding: 4px 10px;
    }
}
